<template>
  <div class="row mt-4">
    <div class="col-4 mt-5 mx-auto">
      <div>
        <img alt="logo"
             src="@/assets/logo-purple.svg"
             align="middle"
             width="50%"
             class="d-block ml-auto mr-auto"/>
      </div>
      <div class="card mt-3">
        <div class="card-body">
          <validation-observer ref="loginForm" v-slot="{invalid}">
            <form @submit.prevent="handleLogin">
              <div v-if="!apiKey"
                   class="form-group">
                <label for="keyInput">API Key</label>
                <validation-provider name="API Key" rules="required" v-slot="{errors}">
                  <input
                      v-model="key"
                      id="keyInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <div class="form-group">
                <label for="nameInput">Nutzername</label>
                <validation-provider name="Nutzername" rules="required" v-slot="{errors}">
                  <input
                      v-model="username"
                      id="nameInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <div class="form-group">
                <label for="passwordInput">Passwort</label>
                <validation-provider name="Passwort" rules="required" v-slot="{errors}">
                  <input
                      v-model="password"
                      id="passwordInput"
                      class="form-control"
                      type="password">
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <div class="form-group">
                <button type="submit"
                        name="general"
                        class="btn btn-primary btn-block"
                        :disabled="loading || invalid">
                    <span v-show="loading"
                          class="spinner-border spinner-border-sm mr-2">
                      ...
                    </span>
                  <span>Login</span>
                </button>
              </div>
            </form>
          </validation-observer>
          <validation-observer ref="serverErrors">
            <validation-provider vid="serverError" v-slot="{errors}">
              <div>
                <span class="form-error">{{ errors[0] }}</span>
              </div>
            </validation-provider>
          </validation-observer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import authenticationService from "@/services/authentication.service"

export default {
  data() {
    return {
      username: null,
      password: null,
      key: null,
      loading: false
    }
  },
  computed: {
    ...mapGetters(['apiKey', 'loggedIn'])
  },
  methods: {
    handleLogin() {
      this.loading = true
      this.$refs.serverErrors.reset()
      if (this.key)
        this.$store.commit('apiKeyDetermined', this.key)
      authenticationService.login(this.username, this.password).then(token => {
        console.log('authenticationService.login() succeeded with token ' + token)
      }).catch(() => {
        this.$refs.serverErrors.setErrors({
          serverError: ['Die Anmeldung ist fehlgeschlagen. Bitte überpfüfen Sie Ihre Angaben!']
        });
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>